.header{
    border-bottom: 1px solid #e3e3e3;
    position: sticky;
    top: 0;
    padding: 10px 16px;
    background: #fff;
    z-index: 2  ;
}
a.logo{
    color: black;
    text-decoration: none;
    font-size: 24px;
}
.nav a{
    color:black;
}
.footer{
background: #efefef;
padding: 20px 0;
margin-top: 20px;
}
.nav a {
    position: relative;
    color: #000;
    text-decoration: none;
  }
  
  .nav a:hover {
    color: #000;
  }
.nav a::before {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #ffdb2d;
    transform: scaleX(0);
    transition: transform 0.3s ease;
  }

  a:hover::before {
    transform: scaleX(1);
  }